
import { Field } from "mint-ui"
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
name: "set",
data() {
return {
users:localStorage.getItem('users')?JSON.parse(localStorage.getItem('users')):'',
show: true,
title: "贷款详情",
touxiang: { src: require("@/assets/images/jl05.png") },
xing: { src: require("@/assets/images/jp_icon.png") },
resdata:[],
addstar:false,
};
},
created() {



this.$axios.post('/api/Lenders_product/product_detail',`data=${JSON.stringify({
session_id:this.users.session_id,
userId:this.users.userId,
product_id:this.$route.query.id
})}`).then((res)=>{
console.log(res)
if(res.code==200){
this.resdata=res.data;
if(res.data.is_collect==1){
this.addstar=true

}
if(res.data.is_collect==0){
this.addstar=false
}
}
}).catch((err)=>{throw err});
},
components: {
XHeader,
Field,

},
methods:{
star(){
this.$axios.post('/api/Lenders_product/collection',`data=${
JSON.stringify({
session_id:this.users.session_id,
userId:this.users.userId,
table:'product',
product_id:this.$route.query.id,
}) }`).then((res)=>{
if(res.code==200){
this.addstar=!(this.addstar);

}

})
.catch(err=>{})
},	KeFuQQ(){
if (plus.os.name == "iOS") {
plus.runtime.launchApplication({
action: "mqq://im/chat?chat_type=wpa&uin=1054564&version=1&src_type=web"
}, function(e) {
plus.nativeUI.confirm("检查到您未安装qq，请先到appstore搜索下载？", function(i) {
if (i.index == 0) {
iosAppstore("itunes.apple.com/cn/app/mqq/");
}
});
});
}else if (plus.os.name == "Android") {
var main = plus.android.runtimeMainActivity();
var Intent = plus.android.importClass('android.content.Intent');
var Uri = plus.android.importClass('android.net.Uri');
var intent = new Intent(Intent.ACTION_VIEW, Uri.parse("mqqwpa://im/chat?chat_type=wpa&uin=1054564"));
main.startActivity(intent);
}
},
}
};



